<template id="todolist">
    <div class="todolist-wrapper">
        <div class="todolist__header">
            <h1><input v-on:keyup="saveName" v-model="name"></h1>
            <button v-on:click="deleteList()" class="show-add-todo-button"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 40 40"><path d="M28 40H11.8c-3.3 0-5.9-2.7-5.9-5.9V16c0-.6.4-1 1-1s1 .4 1 1v18.1c0 2.2 1.8 3.9 3.9 3.9H28c2.2 0 3.9-1.8 3.9-3.9V16c0-.6.4-1 1-1s1 .4 1 1v18.1c0 3.2-2.7 5.9-5.9 5.9zm5.3-35.1h-7.6C25.2 2.1 22.8 0 19.9 0s-5.3 2.1-5.8 4.9H6.5C4.2 4.9 2.4 6.7 2.4 9s1.8 4 4.1 4h26.9c2.3 0 4.1-1.8 4.1-4.1s-1.9-4-4.2-4zM19.9 2c1.8 0 3.3 1.2 3.7 2.9h-7.5c.5-1.7 2-2.9 3.8-2.9zm13.4 9H6.5c-1.1 0-2.1-.9-2.1-2.1 0-1.1.9-2.1 2.1-2.1h26.9c1.1 0 2.1.9 2.1 2.1-.1 1.2-1 2.1-2.2 2.1z"/><path d="M12.9 35.1c-.6 0-1-.4-1-1V17.4c0-.6.4-1 1-1s1 .4 1 1v16.7c0 .5-.5 1-1 1zm14 0c-.6 0-1-.4-1-1V17.4c0-.6.4-1 1-1s1 .4 1 1v16.7c0 .5-.5 1-1 1zm-7 0c-.6 0-1-.4-1-1V17.4c0-.6.4-1 1-1s1 .4 1 1v16.7c0 .5-.5 1-1 1z"/></svg></button>
            <button v-on:click="showAddTodo()" class="show-add-todo-button"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256"><path d="M185.066 128c0 3.534-2.866 6-6.4 6H134v44.666c0 3.534-2.467 6.4-6 6.4-3.534 0-6-2.866-6-6.4V134H77.332c-3.535 0-6.4-2.466-6.4-6s2.866-6 6.4-6H122V77.33c0-3.533 2.466-6.4 6-6.4 3.533 0 6 2.867 6 6.4V122h44.666c3.534 0 6.4 2.466 6.4 6zM256 128C256 57.42 198.58 0 128 0S0 57.42 0 128s57.42 128 128 128 128-57.42 128-128zm-12.8 0c0 63.52-51.68 115.2-115.2 115.2-63.522 0-115.2-51.68-115.2-115.2C12.8 64.478 64.478 12.8 128 12.8c63.52 0 115.2 51.678 115.2 115.2z"/></svg></button>
        </div>
        <ul class="todolist">
            <draggable :list="todos" @end="updateSortOrder()" :options="{'disabled':draggingDisabled}">
                <li v-for="(todo, key) in todos" v-bind:key="todo.sortOrder">
                    <todoItem :todo="todo" :index="key" @update="updateTodo"  @inactive="enableDragging()" @active="disableDragging()" @remove="removeTodo(todo)"></todo-item>
                </li>
            </draggable>
        </ul>
        <button @click="seeAllLists" class="back-button">Back</button>
        <addTodo v-if="this.showAddInput == true" @hideAddTodo="hideAddTodo" @addTodo="addTodo"></add-todo>
    </div>
</template>

<script>
    import todoItem from '../todo-item/Todo-item.vue';
    import draggable from 'vuedraggable';
    import addTodo from '../add-todo/add-todo.vue';
    import store from '../../store';

    export default {
        components: {
            todoItem,
            draggable,
            addTodo,
        },
        data: function() {
            return {
                draggingDisabled: false,
                showAddInput: false,
                name: '',
                todos: [],
                todolistName: '',
            }
        },
        watch: {
            todolistName: function(val) {
                for (var i = 0; i < val.length; i++) {
                    val[i]
                    if ( val[i][".key"] === "name" ) {
                        this.name = val[i][".value"];
                        break;
                    }
                }
            },
        },
        created: function() {
            this.$bindAsArray('todos', store.state.db.ref(store.state.uid).child('lists').child(store.state.chosenList).child("todos").orderByChild("sortOrder"));
            this.$bindAsArray('todolistName', store.state.db.ref(store.state.uid).child('lists').child(store.state.chosenList));
        },
        beforeMount: function() {
            this.todos.sort(function(a, b) {
                return a.sortOrder - b.sortOrder;
            });
        },
        methods: {
            addTodo(text) {
                store.state.listsRef.child('lists').child(store.state.chosenList).child('todos').push({
                    text: text,
                    completed: false,
                    sortOrder: -1
                });
                this.showAddInput = false;
                this.enableDragging();
            },
            deleteList() {
                store.state.listsRef.child('lists').child(store.state.chosenList).remove();
                store.state.chosenList = false;
            },
            disableDragging() {
                this.draggingDisabled = true;
            },
            enableDragging() {
                this.draggingDisabled = false;
            },
            hideAddTodo() {
                this.showAddInput = false;
                this.enableDragging();
            },
            showAddTodo() {
                this.showAddInput = true;
                this.disableDragging();
            },
            removeTodo(todo) {
                store.state.listsRef.child('lists').child(store.state.chosenList).child('todos').child(todo['.key']).remove();
            },
            saveName() {
                store.state.listsRef.child('lists').child(store.state.chosenList).update({name: this.name});
            },
            seeAllLists() {
                store.state.chosenList = false;
            },
            updateTodo(todo) {
                store.state.listsRef.child('lists').child(store.state.chosenList).child('todos').child(todo['.key']).update({completed: todo.completed, text: todo.text});
            },
            updateSortOrder() {
                var keys = [];

                for (var i = 0; i < this.todos.length; i++) {
                    keys.push(this.todos[i][".key"]);
                }

                for (var i = 0; i < keys.length; i++) {
                    store.state.listsRef.child('lists').child(store.state.chosenList).child(keys[i]).update({sortOrder: i});
                }
            },
        },
    }

</script>

<style src="./Todolist.scss"></style>



