import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

const VueFire = require('vuefire');
const Firebase = require('firebase');
Vue.use(VueFire);

const config = {
    apiKey: "AIzaSyCnbmcx_h0MlOSqlsRzrqf9Snkhg1TeNgo",
    authDomain: "todolist-426a4.firebaseapp.com",
    databaseURL: "https://todolist-426a4.firebaseio.com",
    storageBucket: "todolist-426a4.appspot.com",
    messagingSenderId: "802197748637"
};

const firebaseApp = Firebase.initializeApp(config);
const db = firebaseApp.database();

firebaseApp.auth().onAuthStateChanged(firebaseUser => {
    if ( firebaseUser ) {
        store.state.currentUser = firebaseUser.email;
        store.state.uid = firebaseUser.uid;
        store.state.loggedIn = true;
        store.state.listsRef = store.state.db.ref(firebaseUser.uid);
    }
    else {
        store.state.currentUser = false;
        store.state.uid = false;
        store.state.loggedIn = false;
    }
    store.state.loaded = true;
});

const store = new Vuex.Store({
    state: {
        currentUser: false,
        uid: false,
        firebaseApp: firebaseApp,
        db: db,
        chosenList: false,
        loggedIn: false,
        loaded: false,
        listsRef: false,
    },
    mutations: {

    },
})

export default store



