<template>
    <div id="app" v-if="this.loaded == true">
        <div v-if="this.loggedIn == true">
            <lists v-if="this.chosenList == false" :lists="lists"></lists>
            <todolist v-if="this.chosenList"></todolist>
            <logout @logout="logout"></logout>
        </div>
        <div v-if="this.loggedIn == false">
            <login v-if="this.loggedIn == false" @login="login" @signup="signup"></login>
        </div>
    </div>
</template>

<script>
    import todolist from './todolist/Todolist.vue';
    import login from './login/Login.vue';
    import logout from './logout/Logout.vue';
    import store from '../store';
    import lists from './lists/Lists.vue';

    export default {
        components: {
            todolist,
            login,
            logout,
            lists
        },
        data: function() {
            return {
                lists: [],
            }
        },
        computed: {
            chosenList: function () {
                return store.state.chosenList;
            },
            loggedIn: function() {
                return store.state.loggedIn;
            },
            loaded: function() {
                return store.state.loaded;
            }
        },
        watch: {
            loggedIn: function(val) {
                if (val) {
                    this.$bindAsArray('lists', store.state.db.ref(store.state.uid).child("lists").orderByChild("sortOrder"));
                }
            },
        },
        methods: {
            login(email, password) {
                const auth = store.state.firebaseApp.auth();
                const promise = auth.signInWithEmailAndPassword(email, password);

                promise.catch(e => console.log(e.message));
            },
            logout() {
                store.state.firebaseApp.auth().signOut();
            },
            signup(email, password) {
                const auth = store.state.firebaseApp.auth();
                const promise = auth.createUserWithEmailAndPassword(email, password);

                promise.catch(e => console.log(e.message));
            },
        },
    }
</script>

<style src="./App.scss"></style>



