<template>
    <div class="lists-wrapper">
        <h1>Lists</h1>
        <div class="add-list list">
            <input type="text" @keyup.enter="addList" v-model="newListName" placeholder="Name of list...">
            <button @click="addList">Add list</button>
        </div>
        <draggable class="lists" :list="lists" @end="updateSortOrder()">
            <div @click="openList(list)" class="list" v-for="(list, key) in lists">
                <h2 class="name">{{ list.name }}</h2>
            </div>
        </draggable>
    </div>
</template>

<script type="text/javascript">
    import store from '../../store';
    import draggable from 'vuedraggable';

    export default {
        components: {
            draggable,
        },
        props: ['lists'],
        data: function() {
            return {
                newListName: '',
            }
        },
        beforeMount: function() {
            this.lists.sort(function(a, b) {
                return a.sortOrder - b.sortOrder;
            });
        },
        methods: {
            addList() {
                if ( this.newListName.length > 0 ) {
                    store.state.listsRef.child('lists').push({
                        name: this.newListName,
                        items: [],
                        sortOrder: this.lists.length + 1,
                    });
                    this.newListName = '';
                }
            },
            openList(list) {
                store.state.chosenList = list[".key"];
            },
            updateSortOrder() {
                var keys = [];

                for (var i = 0; i < this.lists.length; i++) {
                    keys.push(this.lists[i][".key"]);
                }

                for (var i = 0; i < keys.length; i++) {
                    store.state.listsRef.child("lists").child(keys[i]).update({sortOrder: i});
                }
            }
        },

    }
</script>

<style src="./Lists.scss"></style>



